<template>
    <div class="login login-public ">
        <div class="bgc" :style="bg">
            
        </div>
        <div class="login_footer">
            <p class="login_footer_company">Copyright © www.pengdiikj.com, All Rights Reserved.</p>
            <p class="login_footer_company">西安鹏迪信息科技有限公司</p>
        </div>
        <div style="float:right;">
            <div class="login_content">
                <img style="margin-bottom: 30px; width:313px;" :src="logo">
                <p class="login_content_title">教学质量监控大数据分析平台</p>
                <p class="login_content_title1">Large Data Analysis Platform for Teaching Quality Monitoring</p>
                <!-- <p class="login_content_title-english">Teaching objective management system</p> -->
                <el-form :model="form" status-icon :rules="rules" ref="form" label-width="100px" class="login-ruleForm myForm">
                    <el-form-item prop="username" class="el-form-iteme">
                        <el-input v-model="form.username" autocomplete="off" placeholder="请输入用户名称" >
                            <template slot="prepend" style="text-align: center">
                                <SvgIcon icon-class="iconyonghu" size="31px"></SvgIcon>
                            </template>
                        </el-input>
                    </el-form-item>
                    <el-form-item prop="password">
                        <el-input @keyup.enter.native="submitForm('form')" type="password" v-model="form.password" autocomplete="off" placeholder="请输入登录密码">
                            <template slot="prepend" style="text-align: center;">
                                <SvgIcon icon-class="iconmima" size="31px"></SvgIcon>
                            </template>
                        </el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button @click="submitForm('form')" class="login-button" type="primary" size="medium">登录</el-button>
                        <el-button @click="resetPasswords" type="text" class="login-button-reset">忘记密码</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <!-- <div class="login_footer">
                <p class="login_footer_company">Copyright © www.pengdiikj.com, All Rights Reserved.</p>
                <p class="login_footer_company">西安鹏迪信息科技有限公司</p>
            </div> -->
        </div>
        <!-- 选择角色弹框 -->
        <el-dialog title="选择角色" :visible.sync="dialogFormVisible" :close-on-click-modal='false' width="20%">
          <el-form :model="roleForm" :rules="rule" ref="roleForm">
            <el-form-item prop="role">
              <el-select v-model="roleForm.role" placeholder="请选择角色" size="small" style="width: 100%; height: 40px; line-height: 40px; text-align: left">
                <el-option v-for="item in options"
                  :key="item.code"
                  :label="item.name"
                  :value="item.code">
                </el-option>
              </el-select>
            </el-form-item>
          </el-form>
          <div slot="footer" class="dialog-footer">
            <el-button type="primary" @click="submitRoleForm('roleForm')">确 定</el-button>
          </div>
        </el-dialog>
    </div>
</template>

<script>
import loginRequest from "@/api/login";
export default {
    name: "Login",
    data() {
        const validateName = (rule, value, callback) => {
            if (!value) {
                callback(new Error("请输入用户名称"));
            } else {
                callback();
            }
        };
        const validatePass = (rule, value, callback) => {
            const reg = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,20}$/;
            if (!value ) {
                callback(new Error("请输入登录密码"));
            } else {
                callback();
            }
            // if(!reg.test(value)){
            //     callback(new Error('密码6至20位，必须包含字母与数字'))
            // }else{
            //     callback()
            // }
        };
        return {
            // logImg: "../../static/img/loImg.svg",
            // loginBackground: "../../assets/reg-bg1.png",
            logo: require('@/assets/img/logo.png'),
            bg: {
              backgroundImage: 'url(' + require("@/assets/img/loginbg.png") + ') no-repeat',
              backgroundSize: '100% 100%'
            },
            form: {
                username: "",
                password: ""
            },
            rules: {
                username: [{
                    validator: validateName,
                    trigger: "blur"
                }],
                password: [{
                    validator: validatePass,
                    trigger: "blur"
                }]
            },
            rule: {
              role: [
                { required: true, message:'请选择角色', trigger: 'change' }
              ]
            },
            roleForm: {
              role: ''
            },
            dialogFormVisible: false,
            options: []
        }
    },
    created() {
      localStorage.removeItem("x_token")
      localStorage.removeItem('UrlStr')
      localStorage.removeItem('specialId')
      localStorage.removeItem('versionId')
      localStorage.removeItem('roleId')
      localStorage.removeItem('roleName')
      localStorage.removeItem("userName")
      localStorage.setItem('parentIndex', '0')
      localStorage.setItem('curIndex', '0')
      const res={
          data: {
              logo: "http://114.115.156.243:83/image/2020/03/18/68985100159470796820200318150135.png",
              indexBackdrop: "http://114.115.156.243:83/image/2020/03/18/68986879334154240020200318161217.png"
          }
      }
      res.data.logo ? this.logo = res.data.logo : this.logo = require('@/assets/img/logo.png')
      res.data.indexBackdrop ? this.bg = {backgroundImage: 'url(' + res.data.indexBackdrop + ')', backgroundSize: '100% 100%'} : this.bg = {backgroundImage: 'url(' + require("@/assets/img/loginbg.png") + ')', backgroundSize: '100% 100%'}
      
      ///system/auth/init'
     /* this.$get('/system/cas/login').then(res => {
        console.log(res)
       // res.data.logo ? this.logo = res.data.logo : this.logo = require('@/assets/img/logo.png')
       // res.data.indexBackdrop ? this.bg = {backgroundImage: 'url(' + res.data.indexBackdrop + ')', backgroundSize: '100% 100%'} : this.bg = {backgroundImage: 'url(' + require("@/assets/img/loginbg.png") + ')', backgroundSize: '100% 100%'}
       setTimeout(()=>{
          //window.open('http://authserver.xauat.edu.cn/authserver/login?service=http%3A%2F%2Fstmis.xauat.edu.cn%2FerrorAction%21to_error.action%3Fcode%3D404%26errorUrl%3Dhttp%3A%2F%2F202.200.158.223%2F**')
         //window.location.href='http://authserver.xauat.edu.cn/authserver/login?service=http%3A%2F%2F202.200.158.181%3A80%2Fauth%2Finit'
         //window.open('http://authserver.xauat.edu.cn/authserver/login?service=http%3A%2F%2F202.200.158.181%3A80%2Fauth%2Finit')
       },800)
      })*/
      
     
    },
    computed: {
      roleId() {
        return this.roleForm.role
      }
    },
    watch: {
      roleId(val) {
        if(val) {
          let obj = {}
          obj = this.options.find((item)=>{
            return item.code === val
          })
          localStorage.setItem('roleName', obj.name)
        }
      }
    },
    methods: {
      submitRoleForm(formName) {
        this.$refs[formName].validate(valid => {
          if (valid) {
            localStorage.setItem('roleId', this.roleForm.role)
            this.dialogFormVisible = false
            this.$notify.success({
              title: "success",
              duration: 1500,
              message: '登录成功'
            })
            this.$router.push({
              path: "/main",
              query: {}
            })
           
          }
        })
      },
        submitForm(formName) {
          this.loginpassword=false;
           this.loginusername=false;
            localStorage.removeItem("x_token");
            localStorage.removeItem("userIdentity");
             this.$router.push({
                      path: "/main",
                      query: {}
                    })
            return
            this.$refs[formName].validate(valid => {
                if (valid) {
                    this.$post(loginRequest.loginry, this.form).then(res => {
                      this.showpassword = res.message
                        if (res.status === 1) {
                            localStorage.setItem("x_token", res.data.token);
                            if (this.form.password === "123456") {
                                this.$router.push({
                                    path: "/modifyPassword",
                                    query: {}
                                });
                            } else {
                              if(res.data.roles) {
                                const roles = res.data.roles
                                if(roles.length === 0) {
                                  this.$notify.success({
                                      title: "info",
                                      duration: 1500,
                                      message: '请联系管理员授权!'
                                  })
                                } else if(roles.length === 1) {
                                  localStorage.setItem('roleId', roles[0].code)
                                  let obj = {}
                                  obj = roles.find((item)=>{
                                    return item.code === roles[0].code
                                  })
                                  
                                  localStorage.setItem('roleName', obj.name)
                                  this.dialogFormVisible = false
                                  this.$notify.success({
                                    title: "success",
                                    duration: 1500,
                                    message: '登录成功'
                                  })
                                  
                                  this.$router.push({
                                    path: "/main",
                                    query: {}
                                  })
                                } else if(roles.length > 1) {
                                  this.dialogFormVisible = true
                                  this.options = roles
                                  this.$nextTick(() => {
                                    this.$refs.roleForm.resetFields()
                                  })
                                }
                              }
                            }
                        } else {
                            this.$notify.error({
                              title: "error",
                              duration: 1500,
                              message: res.message
                            });
                        }
                    }).catch(err => {
                        console.log(err);
                    });
                } else {
                    return false;
                }
            });
        },
        resetPasswords() {
            this.$router.push({
                path: "/resetPasswords",
                query: {}
            });
        }
    }
};
</script>

<style lang="scss">
@import "../../styles/login.scss";

.bgc {
    background-size: 100% 100%;
    height: 100%;
    width: 100%;
    float: left;
}
.myForm {
  .el-icon-circle-check:before {
    content: "\e6da";
    background: #cdefe7;
    border-radius: 50%;
    padding: 3px;
    font-size: 12px;
    font-weight: 700;
    color: #03B187;
  }
  .el-icon-circle-close:before {
    content: "\e6db";
    background: #ffdede;
    border-radius: 50%;
    padding: 3px;
    font-size: 12px;
    font-weight: 700;
    color: red;
  }
}

</style>



